<template>
  <div class="flex-center" style="width:100vw;height:100vh;background-color:#2ba06a;">
      <div class="flex-center flex-column" style="margin-bottom:30%;">
        <img src="@/assets/img/icon/logo@3x.png" alt="" style="width:100px;">
        <p class="flex-horizontal" style="color:#fff;margin-top:10px;font-size:18px;">泰瑞5G智慧农业</p>
      </div>
  </div>
</template>

<script>
export default {
  beforeCreate(){
    sessionStorage.setItem('enterRecord',true);
  },
  mounted(){
    let from = {};
    try{
      from = JSON.parse(sessionStorage.getItem('fromUrl'));
    }catch(e){}
    setTimeout(()=>{
      this.$router.push({
        name: from.name,
        query: from.query
      });
    },2000)
  }
}
</script>
